.homesecWrap {
    
    .article2 {
        .row {
            ul {
                display: flex; flex-wrap: wrap;
                padding: 30px 0px;
                li {
                    width: 33.33%;
                    padding: 20px;
                    a {
                        display: flex;
                        img {
                            width: 30%; min-width: 85px; min-height: 85px;
                            margin-right: 10px;
                            border-radius: 10px; border: 5px solid #bbb;
                        }
                        div {
                            align-self: center;
                            strong {
                                font-size: 16px;
                            }
                            p {
                                font-size: 12px;
                            }
                        }
                    }
                }
            }
            .lifeStyle {
                h2 {
                    font-size: 20px;
                }
                ul {
                    display: flex;
                    li {
                        width: 33.33%;
                        text-align: center;
                        padding: 0px 10px;
                        img {
                            width: 100%;
                            border-radius: 10px;
                            margin-bottom: 10px;
                        }
                        strong {

                        }
                    }
                }
            }
        }
    }
    .article3 {
        background: #000; color: #fff;
        padding: 100px 0px;
        .row {
            display: flex;
            div {
                width: 52%;
                &.right {
                    width: 48%;
                    display: flex; flex-direction: column;
                    justify-content: space-between;
                    .rightFst {
                        display: flex; flex-direction: row;
                        // width: 100%;
                    }
                    div {
                        width: 100%;
                    }
                }
                img {
                    width: 100%;
                    padding: 10px 10px 0px;
                    border-radius: 30px 30px 0px 0px;
                    vertical-align: middle;
                }
                p {
                    background: #fff; color: #333;
                    font-weight: bold;
                    margin: 0px 10px; padding: 5px 10px 5px 15px;
                    border-radius: 0px 0px 30px 30px;
                    font-size: 1vw;
                }
            }
        }
    }
}